<template>
  <div class="nav-header">
    <v-toolbar
      color="#f2f2f2"
      dark
      tabs
      class="nav-active"
    >
      <div class="logo">
        <img
          v-lazy="'data:image/png;base64,'"/>
      </div>
      <v-text-field
        append-icon="mic"
        class="mx-3"
        flat
        label="Search"
        prepend-inner-icon="search"
        solo-inverted
      ></v-text-field>

      <template v-slot:extension>
        <v-tabs
          v-model="active"
          active-class="nav-active"
          centered
          color="transparent"
          slider-color="rgb(237, 91, 0)"
        >
          <v-tab
            v-for="(item,index) in tabs"
            :key="index"
            color="red"
          >
            {{ item.name }}
          </v-tab>
        </v-tabs>
      </template>
      <div class="user-avatar">
        <img
          v-lazy="'data:image/png;base64,'"
          alt="">
      </div>
    </v-toolbar>

    <v-tabs-items v-model="active">

      <home-tabs v-for="(item,index) in apis"
                 :key="index" :className="'card'+(index+1)" :apiUrl="item"></home-tabs>
    </v-tabs-items>
    <!--回到顶部-->
    <div class="back-top" @click="backTop">
      <img src="../assets/top.png" alt="">
    </div>
  </div>
</template>

<script>
  import HomeTabs from '@/components/HomeSubPages/HomeTabs'

  export default {
    components: {HomeTabs},
    data() {
      return {
        tabs: [],
        apis: ['recommend', 'phone', 'smart', 'tv', 'computer', 'appliance', 'periphery'],
        active: null,
      }
    },
    created() {
      this.axios.get('/api/home/tabs.json').then((res) => {
        if (res.data.result == 'ok') {
          this.tabs = res.data.data.tabs
        }
      })
    },
    activated() {//keep-alive 组件激活时调用。
      window.addEventListener('scroll', this.scrollEvent)
    },
    deactivated() {//keep-alive 组件停用时调用。
      window.removeEventListener('scroll', this.scrollEvent)
    },
    methods: {
      backTop() {
        document.body.scrollIntoView({
          block: 'start',
          behavior: 'auto'
        })
      },
      scrollEvent() {
        let top = document.querySelector('.nav-header').getBoundingClientRect().top
        if (top < -800) {
          document.querySelector('.back-top').style.display = 'block'
        } else {
          document.querySelector('.back-top').style.display = 'none'
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .nav-header {
    .nav-active {
      padding-top: .2rem;
    }

    .back-top {
      position: fixed;
      right: .26rem;
      bottom: 1.4rem;
      width: .7rem;
      height: .7rem;
      z-index: 999;
      display: none;

      img {
        max-width: 100%;
      }
    }
  }

</style>
